import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios';

// Define the types for your API responses
export interface ApiResponse<T> {
  data: T;
  message: string;
  status: string;
}

const apiClient: AxiosInstance = axios.create({
  baseURL: import.meta.env.VITE_API_BASE_URL || 'http://localhost:8080/api',
  headers: {
    'Content-Type': 'application/json',
  },
  timeout: 10000, // 10 seconds timeout
});

apiClient.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => Promise.reject(error),
);

apiClient.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      localStorage.removeItem('token');
      window.location.href = '/login'; // Redirect to login page
    }
    return Promise.reject(error);
  },
);

// Modify the postApi function to use a more specific type
export async function postApi<T, D>(url: string, data: D): Promise<T> {
  const response: AxiosResponse<T> = await axios.post(url, data);
  return response.data; // Return only the `data` property
}

export interface LoginResponse {
  token: string;
}

const api = {
  get: async <T>(url: string, config: AxiosRequestConfig = {}): Promise<T> => {
    const response: AxiosResponse<T> = await apiClient.get(url, config);
    return response.data;
  },

  post: async <T, D>(
    url: string,
    data: D,
    config: AxiosRequestConfig = {},
  ): Promise<T> => {
    const response: AxiosResponse<T> = await apiClient.post(url, data, config);
    return response.data;
  },

  put: async <T, D>(
    url: string,
    data: D,
    config: AxiosRequestConfig = {},
  ): Promise<T> => {
    const response: AxiosResponse<T> = await apiClient.put(url, data, config);
    return response.data;
  },

  delete: async <T>(
    url: string,
    config: AxiosRequestConfig = {},
  ): Promise<T> => {
    const response: AxiosResponse<T> = await apiClient.delete(url, config);
    return response.data;
  },
};

export default api;
