import React, { useState } from 'react';
import {
  Box,
  Button,
  CircularProgress,
  Container,
  TextField,
  Typography,
  Alert,
  List,
  ListItem,
  ListItemText,
  Drawer,
  IconButton,
  Divider,
  ListItemIcon,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import HomeIcon from '@mui/icons-material/Home';
import InfoIcon from '@mui/icons-material/Info';
import ContactMailIcon from '@mui/icons-material/ContactMail';
import useFetch from '../../hooks/useFetch';
import useAuth from '../../hooks/useAuth';
import Clock from './Clock'; // Ensure Clock component exists

// Define types for dashboard items
interface DashboardItem {
  empname: string;
  birth_date: string;
  emp_des: string;
}

const Login = () => {
  const { login } = useAuth();
  const [form, setForm] = useState({ username: '', password: '' });
  const [loginError, setLoginError] = useState('');
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isLoggingIn, setIsLoggingIn] = useState(false);

  // Fetch data using custom hook
  const {
    data: reminders,
    loading: remindersLoading,
    error: remindersError,
  } = useFetch<DashboardItem[]>('/dashboard/reminders');
  const {
    data: holidays,
    loading: holidaysLoading,
    error: holidaysError,
  } = useFetch<DashboardItem[]>('/dashboard/holidays');
  const {
    data: birthdays,
    loading: birthdaysLoading,
    error: birthdaysError,
  } = useFetch<DashboardItem[]>('/dashboard/birthdays');

  const toggleMenu = () => setIsMenuOpen((prev) => !prev);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { id, value } = e.target;
    setForm((prev) => ({ ...prev, [id]: value }));
  };

  const isLoginFormValid = () => {
    if (!form.username || !form.password) {
      setLoginError('Both username and password are required.');
      return false;
    }
    return true;
  };

  const handleLogin = async () => {
    if (!isLoginFormValid()) return;

    setIsLoggingIn(true);
    setLoginError('');

    try {
      const response = await fetch('/auth/login', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(form),
      });

      if (!response.ok) {
        throw new Error('Invalid username or password.');
      }

      const data: { token: string } = await response.json();
      login(data.token);
      window.location.href = '/post-login';
    } catch (error) {
      setLoginError('Invalid username or password.');
    } finally {
      setIsLoggingIn(false);
    }
  };

  const menuItems = [
    { text: 'Home', icon: <HomeIcon />, link: '/' },
    { text: 'About', icon: <InfoIcon />, link: '/about' },
    { text: 'Contact', icon: <ContactMailIcon />, link: '/contact' },
  ];

  const renderList = (
    title: string,
    items: DashboardItem[] | undefined,
    placeholder: string,
  ) => (
    <Box mb={3}>
      <Typography variant="h6" gutterBottom>
        {title}
      </Typography>
      <List>
        {items?.length ? (
          items.map((item, index) => (
            <ListItem key={index}>
              <ListItemText
                primary={`${item.empname} (${item.birth_date})`}
                secondary={item.emp_des}
              />
            </ListItem>
          ))
        ) : (
          <Typography variant="body2" color="textSecondary">
            {placeholder}
          </Typography>
        )}
      </List>
    </Box>
  );

  const isLoading = remindersLoading || holidaysLoading || birthdaysLoading;
  const fetchError = remindersError || holidaysError || birthdaysError;

  return (
    <Container maxWidth="md" sx={{ mt: 5 }}>
      {/* Menu Section */}
      <Drawer anchor="left" open={isMenuOpen} onClose={toggleMenu}>
        <Box sx={{ width: 250 }}>
          <Typography variant="h6" sx={{ p: 2, textAlign: 'center' }}>
            Menu
          </Typography>
          <Divider />
          <List>
            {menuItems.map((item, index) => (
              <ListItem
                key={index}
                component="a"
                href={item.link}
                sx={{
                  textDecoration: 'none',
                  color: 'inherit',
                }}
              >
                {item.icon && <ListItemIcon>{item.icon}</ListItemIcon>}
                <ListItemText primary={item.text} />
              </ListItem>
            ))}
          </List>
        </Box>
      </Drawer>
      <IconButton onClick={toggleMenu} sx={{ mb: 3 }}>
        <MenuIcon />
      </IconButton>

      {/* Clock Section */}
      <Box mb={5}>
        <Clock />
      </Box>

      {/* Dashboard Sections */}
      {isLoading ? (
        <Box textAlign="center" mb={3}>
          <CircularProgress />
        </Box>
      ) : (
        <>
          {renderList('Reminders', reminders, 'No reminders available')}
          {renderList('Birthdays', birthdays, 'No birthdays available')}
          {renderList('Holidays', holidays, 'No holidays available')}
        </>
      )}

      {fetchError && (
        <Alert severity="error">{fetchError || 'An error occurred.'}</Alert>
      )}

      {/* Login Section */}
      <Box mt={5}>
        <Typography variant="h5" gutterBottom>
          Login
        </Typography>
        <TextField
          fullWidth
          id="username"
          label="Username"
          variant="outlined"
          margin="normal"
          value={form.username}
          onChange={handleChange}
        />
        <TextField
          fullWidth
          id="password"
          label="Password"
          type="password"
          variant="outlined"
          margin="normal"
          value={form.password}
          onChange={handleChange}
        />
        {loginError && (
          <Alert severity="error" sx={{ mt: 2 }}>
            {loginError}
          </Alert>
        )}
        <Button
          fullWidth
          variant="contained"
          color="primary"
          sx={{ mt: 3 }}
          onClick={handleLogin}
          disabled={isLoggingIn}
        >
          {isLoggingIn ? <CircularProgress size={24} /> : 'Login'}
        </Button>
      </Box>
    </Container>
  );
};

export default Login;
